body {
	overflow-y: hidden;
}

.plans {
	background-image: url("../images/Path-408.png");
	background-size: contain;
	background-repeat: no-repeat;
	object-fit: contain;
	padding-top: 50px;
	height: auto;
	overflow-x: hidden;

	.row {
		background: $mainLightGreen;
		align-items: center;
		padding-top: 50px;
		border-radius: 20px;
	}

	.back_img {
		background-image: url("../images/Path-363.png");
		object-fit: contain;
		background-size: contain;
		background-repeat: no-repeat;

	}

	.plans__img {
		padding: 50px;
	}


	&__features {
		list-style: none;
		padding: 0;

		li {
			position: relative;
			padding-right: 1.5rem;
			margin-bottom: 1rem;
			color: #000;
			font-size: 14px;

			&::before {
				position: absolute;
				content: '';
				width: .85rem;
				height: .85rem;
				border-radius: 50%;
				background: $mainOrange;
				top: 50%;
				transform: translateY(-50%);
				right: 0;

			}
		}
	}
}

.section {
	width: 100%;
	height: auto;
	border-bottom: 1px solid #e0e0e0;
}




.testimonialSection {
	border: 0px;
	padding: 95px 0px;
}


.item .card h5 {
	font-weight: bold;
	font-size: 15px;
}

.item .card p {
	font-weight: bold;
	font-size: 12px;
}

.item .card .img1 {
	position: relative;
}

