@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap');

#testimonial-area {

	.section-heading h2 {
		font-size: 48px;
		line-height: 58px;
	}
}

.testi-wrap {
	position: relative;
	height: 725px;
	margin-top: -80px;
}

.client-single {
	margin-top: 20px;
	text-align: center;
	position: absolute;
	-webkit-transition: all 1s ease;
	transition: all 1s ease;
}

.client-info,
.client-comment {
	-webkit-transition: all 0.3s ease;
	transition: all 0.3s ease;
}

.client-single {
	&.inactive {

		.client-comment,
		.client-info {
			display: none;
		}

		.client-comment,
		.client-info {
			opacity: 0;
			visibility: hidden;
		}
	}

	&.position-1 {
		-webkit-transform: scale(0.65);
		transform: scale(0.65);
	}

	&.position-2 {
		left: -40px;
		top: 105px;
	}

	&.position-3 {
		left: -60px;
		top: 240px;
		-webkit-transform: scale(0.4) !important;
		transform: scale(0.4) !important;
	}

	&.position-4 {
		left: 55px;
		top: 380px;
	}

	&.position-5 {
		top: 30px;
		right: 55px;
	}

	&.position-6 {
		top: 225px;
		right: -40px;
	}

	&.position-7 {
		top: 400px;
		right: 45px;
		-webkit-transform: scale(0.4) !important;
		transform: scale(0.4) !important;
	}

	&.active {
		top: 10%;
		left: 50%;
		-webkit-transform: translateX(-50%);
		transform: translateX(-50%);
		z-index: 10;
		width: 70%;

		.client-comment,
		.client-info {
			-webkit-transition-delay: 0.6s;
			transition-delay: 0.6s;
		}
	}

	&:not(.active) {
		-webkit-transform: scale(0.55);
		transform: scale(0.55);
		z-index: 99;
	}

	&.active .client-img {
		width: 160px;
		height: 160px;
		margin: 0 auto 24px;
		position: relative;

		&:before {
			border-radius: 100%;
			content: '';
			background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(157, 91, 254)), to(rgb(56, 144, 254)));
			background-image: linear-gradient(180deg, rgb(157, 91, 254) 0%, rgb(56, 144, 254) 100%);
			padding: 5px;
			width: 160px;
			height: 160px;
			top: -4px;
			left: 0px;
			position: absolute;
			z-index: -1;
		}
	}

	.client-img img {
		width: 150px;
		border-radius: 50%;
		border: 8px solid #d1e9ff;
		cursor: pointer;
	}

	&.active .client-img img {
		max-width: 160px;
		margin: 0 auto 24px;
		border: 0;
	}
}

.client-comment {
	padding: 0 30px;

	h3 {
		font-size: 22px;
		line-height: 32px;
		color: #505b6d;
	}

	span i {
		font-size: 60px;
		color: #0084ff;
		margin: 40px 0 24px;
		display: inline-block;
	}
}

.client-info {
	background-color: #c9e8c4;
	padding: 10px;
	border-radius: 30px;
	margin: 0 auto;

	h3 {
		color: #000;
		font-weight: 600;
		margin-bottom: 4px;
		font-size: 20px;

	}

	p {
		color: #F4775C;
		font-weight: bold;
		text-transform: uppercase;
		font-size: 16px;
	}
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
	#testimonial-area .section-heading h2 {
		font-size: 30px;
	}

	.client-comment h3 {
		font-size: 18px;
		line-height: 28px;
	}

	.client-single.active {
		width: 60%;
	}

	.client-single:not(.active) {
		-webkit-transform: scale(0.55);
		transform: scale(0.35);
	}

	.client-single.position-3,
	.client-single.position-7 {
		-webkit-transform: scale(.30) !important;
		transform: scale(.30) !important;
	}

	.client-single.active .client-img img {
		max-width: 100px;
	}

	.client-single.active .client-img::before {
		padding: 5px;
		width: 108px;
		height: 108px;
		top: -4px;
		left: 6px;
	}

	.client-single.active .client-img {
		width: 120px;
		height: 100px;
	}

	.testi-wrap {
		height: 580px;
	}

	#testimonial-area {
		padding: 100px 0 0;
	}
}

